* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: Roboto, -apple-system, system-ui, sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.todo {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 750px;
  height: 530px;
  transform: translate(-50%, -50%);
  font-size: 14px;
}